/* Google Fonts - Fira Code + Fira Sans for professional admin dashboard */
@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600;700&family=Fira+Sans:wght@300;400;500;600;700&display=swap');

/* Design System Tokens */
:root {
  /* Primary Colors - Purple Theme */
  --primary-color: #7C3AED;
  --primary-hover: #6D28D9;
  --primary-light: #A78BFA;
  --primary-lighter: #DDD6FE;
  
  /* Secondary Colors */
  --secondary-color: #A78BFA;
  --accent-color: #F97316;
  --accent-hover: #EA580C;
  
  /* Background Colors */
  --bg-primary: #FAF5FF;
  --bg-secondary: #F3E8FF;
  --bg-card: #FFFFFF;
  --bg-sidebar: linear-gradient(180deg, #7C3AED 0%, #5B21B6 100%);
  
  /* Text Colors */
  --text-primary: #4C1D95;
  --text-secondary: #6B21A8;
  --text-muted: #7C3AED;
  --text-light: #A78BFA;
  
  /* Status Colors */
  --success-color: #10B981;
  --warning-color: #F59E0B;
  --error-color: #EF4444;
  --info-color: #3B82F6;
  
  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgba(124, 58, 237, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(124, 58, 237, 0.1), 0 2px 4px -1px rgba(124, 58, 237, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(124, 58, 237, 0.1), 0 4px 6px -2px rgba(124, 58, 237, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(124, 58, 237, 0.1), 0 10px 10px -5px rgba(124, 58, 237, 0.04);
  
  /* Transitions */
  --transition-fast: 150ms ease;
  --transition-normal: 250ms ease;
  --transition-slow: 350ms ease;
  
  /* Border Radius */
  --radius-sm: 6px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --radius-xl: 16px;
}

/* Global Styles */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Fira Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-primary);
  color: var(--text-primary);
}

code {
  font-family: 'Fira Code', source-code-pro, Menlo, Monaco, Consolas, monospace;
}

/* Enhanced Ant Design Overrides */
.ant-card {
  border-radius: var(--radius-lg) !important;
  box-shadow: var(--shadow-md) !important;
  border: 1px solid var(--primary-lighter) !important;
  transition: all var(--transition-normal) !important;
}

.ant-card:hover {
  box-shadow: var(--shadow-lg) !important;
  transform: translateY(-2px);
}

.ant-table {
  border-radius: var(--radius-lg) !important;
  overflow: hidden;
}

.ant-table-thead > tr > th {
  background: var(--bg-secondary) !important;
  color: var(--text-primary) !important;
  font-weight: 600 !important;
  font-family: 'Fira Code', monospace !important;
}

.ant-table-tbody > tr:hover > td {
  background: var(--bg-secondary) !important;
}

.ant-btn-primary {
  background: var(--primary-color) !important;
  border-color: var(--primary-color) !important;
  box-shadow: var(--shadow-sm) !important;
  transition: all var(--transition-fast) !important;
}

.ant-btn-primary:hover {
  background: var(--primary-hover) !important;
  border-color: var(--primary-hover) !important;
  transform: translateY(-1px);
  box-shadow: var(--shadow-md) !important;
}

.ant-modal-content {
  border-radius: var(--radius-xl) !important;
  overflow: hidden;
}

.ant-modal-header {
  background: var(--bg-secondary) !important;
  border-bottom: 1px solid var(--primary-lighter) !important;
}

.ant-modal-title {
  color: var(--text-primary) !important;
  font-family: 'Fira Code', monospace !important;
}

/* Page title styling */
h2 {
  font-family: 'Fira Code', monospace;
  color: var(--text-primary);
  font-weight: 600;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--primary-light);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}
